import moment from "moment";
export default {
  data() {
    return {
      strategyList: []
    };
  },
  created() {
    this.$axios({
      url: "/posts/recommend?id=" + this.$route.query.id
    }).then(res => {
      console.log("相关攻略", res.data.data);
      this.strategyList = res.data.data;
    });
  },
  //   管道过滤器，渲染发布时间
  filters: {
    setTime(date) {
      return moment(date).format("YYYY-MM-DD HH:mm");
    }
  }
};
